<template>
    <div>
        <van-cell-group title="增加学习机">
            <van-field v-model="form.cname" required clearable label="名称" placeholder="请输入名称"/>
            <van-field v-model="form.attribute" required readonly label="类型" @click="showAttrs = true"/>
            <van-field v-model="form.ctype" required readonly label="属性" @click="showType = true"/>
            <van-field v-model="form.pp" type="number" required clearable label="PP" placeholder="请输入PP"/>
            <van-field v-model="form.shooting" type="number" required clearable label="命中率" placeholder="请输入命中率"/>
            <van-field v-model="form.power" type="number" required clearable label="威力" placeholder="请输入威力"/>
            <van-field v-model="form.tooltip" required clearable label="战斗效果" placeholder="请输入战斗效果"/>
            <van-field v-model="form.detailed" clearable label="详情" placeholder="请输入详情"/>

        </van-cell-group>

        <van-popup v-model="showAttrs" position="bottom">
            <van-picker
                    show-toolbar
                    :columns="attrs"
                    @cancel="showAttrs = false"
                    @confirm="onConfirm1"
            />
        </van-popup>

        <van-popup v-model="showType" position="bottom">
            <van-picker
                    show-toolbar
                    :columns="types"
                    @cancel="showType = false"
                    @confirm="onConfirm2"
            />
        </van-popup>
        <div style="margin: 16px">
            <van-button type="danger" size="large" @click="save">保存</van-button>
            <van-button type="info" style="margin-top: 8px" size="large" @click="reset">重置</van-button>
        </div>
    </div>


</template>

<script>
    export default {
        data() {
            return {
                attrs: ['物理', '特殊', '变化'],
                showAttrs: false,

                types: ['一般', '格斗', '飞行', '毒', '地上', '岩石', '虫',
                    '幽灵', '钢', '火', '水', '草', '电', '超能力', '冰', '龙', '恶', '妖精'],
                showType: false,
                AV: require('leancloud-storage'),
                form: {
                    "attribute": "物理",
                    "pp": '',
                    "detailed": "",
                    "shooting": '100',
                    "power": '',
                    "cname": "",
                    "ctype": "一般",
                    "tooltip": ""
                }
            }
        },
        created() {

        },
        methods: {
            onConfirm1(value) {
                this.form.attribute = value;
                this.showAttrs = false;
            },
            onConfirm2(value) {
                this.form.ctype = value;
                this.showType = false;
            },
            save() {
                let data = this.form;
                var skill = new this.AV.Object('skill');
                skill.set('attribute', data.attribute);
                skill.set('pp', parseInt(data.pp));
                skill.set('detailed', data.detailed);
                skill.set('shooting', parseInt(data.shooting));
                skill.set('power', parseInt(data.power));
                skill.set('cname', data.cname);
                skill.set('ctype', data.ctype);
                skill.set('tooltip', data.tooltip);

                skill.save().then(function (skill) {
                    // 成功保存之后，执行其他逻辑
                    this.$toast.success('保存成功');
                }, function (error) {
                    // 异常处理
                    this.$toast.fail('保存失败' + error);
                });
            },
            reset() {
                this.form = {
                    "attribute": "物理",
                    "pp": '',
                    "detailed": "",
                    "shooting": '',
                    "power": '100',
                    "cname": "",
                    "ctype": "一般",
                    "tooltip": ""
                }
            }
        }
    }
</script>

